.navigation {
  background-color: white;
}
.navigation__container {
  background-color: white;
  width: 1240px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-around;
  padding: 0 20px;
  transition: 1s;
}

@media (max-width:700px) {
  .navigation__container {
    flex-direction:column;
    gap: 10px;
    justify-content: center;
    align-items: center;
    transition: 1s;
    padding: 20px 0;
  }
}

.navigation__logo {
  width: 150px;
  height: 80px;
  background: url('./../../logo.png') no-repeat;
  background-size: contain;  
}
.navigation__container-stage {
  display: flex;
  gap:20px;
}
.navigation__product {
  display: flex;
  gap: 10px;
  flex-direction: column;
  align-items: center;
}
.navigation__language-ukr {
  height: 20px;
  width: 20px;
  background: url('../../Ukraine.png');
}
.navigation__product-current_stage {
  background-color: rgb(70, 70, 250);
  border: 1px solid rgb(70, 70, 250);
  padding: 2px 10px;
  color: white;
  border-radius: 50%;
  position: relative;
}
.navigation__container-select {
  display: flex;
  gap: 10px;
}
.navigation__product-current_stage::after {
  content: "";
  top: 12px;
  left: 30px;
  position: absolute;
  width: 80px;
  height: 0.2px;
  border: 1px dashed rgb(124, 124, 124);
}
.navigation__product-stage {
  border: 1px solid rgb(124, 124, 124);
  padding: 2px 10px;
  color: rgb(124, 124, 124);
  border-radius: 50%;
}
.navigation__order {
  display: flex;
  gap: 10px;
  align-items: center;
  flex-direction: column;
}
.navigation__product-text {
  font-size: 12px;
}

.checked {
  color: rgb(124, 124, 124);
}
